/**
 * Flop 
 * - 
 * - Flop
 * - 
 */
import sleep from '../utils/sleep';
import playCardSound from './sound';

export const handleFlopBet = async ({
  deck,
  dispatch,
  setBoardCardLoadCallback,
  cards,
}) => {
  let currentBoard = cards?.board ? [...cards.board] : [];
  for (let i = 4; i <= 6; i++) {
    const updatedBoard = [...currentBoard, deck[i]];
    await new Promise((resolve) => {
      dispatch({
        type: 'SET_CARDS',
        who: 'board',
        cards: updatedBoard,
      });
      setBoardCardLoadCallback(() => resolve);
    });
    playCardSound();
    await sleep(300);
    currentBoard = updatedBoard; //  Promise
  }
  dispatch({ type: 'SET_PHASE', phase: 'flop' });
};

/**
 * Turn 
 * - 
 * - Turn
 * - 
 */
export const handleTurnBet = async ({
  deck,
  dispatch,
  setBoardCardLoadCallback,
  cards,
}) => {
  const updatedBoard = [...(cards?.board || []), deck[7]];
  await new Promise((resolve) => {
    dispatch({
      type: 'SET_CARDS',
      who: 'board',
      cards: updatedBoard,
    });
    setBoardCardLoadCallback(() => resolve);
  });
  playCardSound();
  await sleep(300);
  dispatch({ type: 'SET_PHASE', phase: 'turn' });
};

export const handleCheckTurn = handleTurnBet; // 

/**
 * River 
 * - 
 * - River
 * - 
 * -  "showdown" 
 */
export const handleRiverBet = async ({
  deck,
  dispatch,
  setBoardCardLoadCallback,
  cards,
}) => {
  const updatedBoard = [...(cards?.board || []), deck[8]];
  await new Promise((resolve) => {
    dispatch({
      type: 'SET_CARDS',
      who: 'board',
      cards: updatedBoard,
    });
    setBoardCardLoadCallback(() => resolve);
  });
  playCardSound();
  await sleep(300);
  dispatch({ type: 'SET_PHASE', phase: 'showdown' });
  dispatch({ type: 'SET_SHOWDOWN', value: true });
};

export const handleCheckRiver = handleRiverBet; // 

/**
 * 
 * - 
 * -  "folded" 
 */
export const handleFold = ({
  dispatch,
  deck,
  playerCards,
  dealerCards,
  bets,
  onHandComplete,
  onResult,
}) => {
  dispatch({ type: 'SET_FOLDED', value: true });
  //  5
  dispatch({
    type: 'SET_CARDS', //   OK
    who: 'board',
    cards: [
      // Flop3 + Turn + River
      deck[4],
      deck[5],
      deck[6],
      deck[7],
      deck[8],
    ],
  });

  const communityCards = [deck[4], deck[5], deck[6], deck[7], deck[8]];

  dispatch({ type: 'SET_PHASE', phase: 'showdown' });
  dispatch({ type: 'SET_SHOWDOWN', value: true });

  /* =====  ===== */
  const totalBet =
    bets.ante + bets.bonus + bets.jackpot + bets.flop + bets.turn + bets.river;

  /*   Fold  RESULT   */
  onResult?.({
    endedBy: 'fold',
    winnerText: 'You folded',
    payoutRows: [{ label: 'TOTAL BET', value: `$${totalBet}` }],
    total: `-$${totalBet}`,
  });
  /*  */

  onHandComplete?.({
    playerCards,
    dealerCards,
    community: communityCards,
    resultText: 'Fold',
    payout: -totalBet, // 
    endedBy: 'fold',
    playerWins: false,
    tie: false,
    totalBet: totalBet, // 
    bonusWin: 0,
    jackpotWin: 0,
  });
};
