// src/components/ShowdownResult.jsx
import { miniCardSrc } from '../utils/cardImages';

const renderMini = (ids) =>
  (ids ?? []).map((id) => (
    <img key={id} src={miniCardSrc(id)} className="mini" alt={id} />
  ));

export default function ShowdownResult({ resultText, style = {} }) {
  /*  */
  if (!resultText) return null;

  /* ---------  --------- */
  const {
    endedBy = '', // 'showdown' | 'fold'
    winnerText = '',
    payoutRows = [],
    total = '',
    playerBest = [], // ["9H","KD",] 5
    dealerBest = [], // ["QS","QC",] 5
    playerRank = '',
    dealerRank = '',
  } = resultText;

  /* --------- Fold  --------- */
  if (endedBy === 'fold') {
    return (
      <div
        className="result-box"
        style={{
          fontFamily: '"Inter", sans-serif',
          fontSize: '1.4rem',
          color: 'crimson',
          fontWeight: 'bold',
          ...style,
        }}
      >
        FOLD...Ante  Bonus 
      </div>
    );
  }

  /* ---------  --------- */
  if (!payoutRows.length) return null; // 

  return (
    <div
      className="result-box"
      style={{
        fontFamily: '"Inter", sans-serif',
        fontSize: '1.25rem',
        lineHeight: 1.4,
        whiteSpace: 'pre-wrap',
        ...style,
      }}
    >
      {/*  /  5  */}
      <div style={{ marginBottom: '0.3em' }}>
         : {renderMini(playerBest)}
      </div>
      <div style={{ marginBottom: '0.1em', fontWeight: 600 }}>
         : {playerRank}
      </div>
      <div style={{ marginBottom: '0.5em' }}>
         : {renderMini(dealerBest)}
      </div>
      <div style={{ marginBottom: '0.3em', fontWeight: 600 }}>
         : {dealerRank}
      </div>

      {/*  */}
      <strong>{winnerText}</strong>

      {/*    */}
      <table className="payout">
        <thead>
          <tr>
            <th colSpan={2}> </th>
          </tr>
        </thead>
        <tbody>
          {payoutRows.map(({ label, value }) => (
            <tr key={label}>
              <td className="cell-label">{label}</td>
              <td className="val">{value}</td>
            </tr>
          ))}
        </tbody>
        <tfoot>
          <tr>
            <td>
              <strong>TOTAL RETURN</strong>
            </td>
            <td className="val">{total}</td>
          </tr>
        </tfoot>
      </table>
    </div>
  );
}
