// src/components/Chip.jsx
import React from 'react';

/**
 * props
 * - value      :  (number)
 * - imageSrc   :  URL
 * - onClick    : 
 * - style      : 
 * - highlight  : boolean    true 
 */
const Chip = ({ value, imageSrc, onClick, style = {}, highlight = false }) => {
  return (
    <img
      src={imageSrc}
      alt={`$${value} chip`}
      onClick={onClick}
      className={highlight ? 'highlight-chip' : ''}
      style={{
        width: '70px',
        height: '70px',
        cursor: 'pointer',
        margin: 0,
        ...style,
      }}
    />
  );
};

export default Chip;
