// components/RenderCard.js
import React from 'react';

/**
 * 
 * @param {string} card - ID ("9H" )
 * @param {object} pos -  {top, left, scale}
 * @param {boolean} faceDown - true
 * @param {string} keyProp - React key ()
 * @param {function} onLoad - 
 */
const RenderCard = ({ card, pos, faceDown = false, keyProp, onLoad }) => {
  return (
    <div
      key={keyProp}
      className="card-abs"
      style={{
        left: pos.left, //   px 
        top: pos.top,
        width: 100, // DIM.CARD_W 
        height: 140, // DIM.CARD_H 
        position: 'absolute',
      }}
    >
      <img
        src={process.env.PUBLIC_URL + `/cards/${faceDown ? 'back' : card}.png`}
        alt={card}
        style={{ width: '100%', height: '100%' }}
        onLoad={onLoad}
      />
    </div>
  );
};

export default RenderCard;
