// src/components/CardSlot.jsx
import React from 'react';
import { DIM } from '../constants/layoutConfig';

export default function CardSlot({ style }) {
  // style  {top, left} 
  const { top, left } = style;

  return (
    <div
      className="card-slot-frame"
      style={{
        left: left, //   px 
        top: top,
        width: DIM.CARD_W, //  
        height: DIM.CARD_H,
        zIndex: 0,
      }}
    />
  );
}
